<template>
  <form action="javascript:void(0)">
    <NavSearchInput :class="{'show': showInput}" />
    <NavSearchButton @click="searchInput" />
  </form>
</template>

<script>
import NavSearchButton from './NavSearchButton.vue';
import NavSearchInput from './NavSearchInput.vue';

export default {
  components: {
    NavSearchButton,
    NavSearchInput,
  },
  data() {
    return {
      showInput: false,
    };
  },
  methods: {
    searchInput() {
      this.showInput = !this.showInput;
    },
  },
  name: 'NavSearch',
};
</script>

