<template>
  <div class="progress-bar">
    <div class="progress-bar__content">
      <div class="progress-bar__up progress-bar__button up">
        <ButtonThumbsUp/>
       {{positiveValueThumb}}%
      </div>
      <div class="progress-bar__down progress-bar__button down">
        <ButtonThumbsDown/>
        {{negativeValueThumb}}%
      </div>
      <div class="progress-bar__width" :style='`width: ${progressBarWidth}%`'></div>
    </div>
  </div>
</template>

<script>
import ButtonThumbsDown from '../buttons/ButtonThumbsDown.vue';
import ButtonThumbsUp from '../buttons/ButtonThumbsUp.vue';

export default {
  components: {
    ButtonThumbsDown,
    ButtonThumbsUp,
  },
  name: 'ProgressBar',
  props: {
    negativeValue: Number,
    positiveValue: Number,
  },
  computed: {
    negativeValueThumb() {
      const totalVotes = (this.positiveValue + this.negativeValue);
      const totalNegativeValue = Math.round((this.negativeValue * 100) / (totalVotes));
      return totalNegativeValue;
    },
    positiveValueThumb() {
      const totalVotes = (this.positiveValue + this.negativeValue);
      const totalPositiveValue = Math.round((this.positiveValue * 100) / (totalVotes));
      return totalPositiveValue;
    },
    progressBarWidth() {
      const totalVotes = (this.positiveValue + this.negativeValue);
      const negativePercent = (this.negativeValue * 100) / totalVotes;
      const positivePercent = (this.positiveValue * 100) / totalVotes;
      let totalPercent = 0;

      if (positivePercent > 50) {
        totalPercent = positivePercent;
      } else {
        totalPercent = negativePercent;
      }
      return totalPercent;
    },
  },
};
</script>
