<template>
  <div class="cards__carousel">
    <SelectGrid @change="selectValue" />
    <ul :class="[selectedGrid ? 'grid' : 'list']" class="cards__container">
      <li class="cards__item" v-for="(item, index) in cards" :key="index">
        <img loading="lazy" class="cards__item-img" :src=item.picture alt="" />
        <div class="cards__item-thumbs">
          <ButtonThumbsUp v-if='item.votes.positive > item.votes.negative' />
          <ButtonThumbsDown v-if='item.votes.negative > item.votes.positive' />
        </div>
        <div class="cards__content">
          <h2 class="cards__item-name">{{item.name}}</h2>
          <p class="cards__item-desc">{{item.description}}</p>
          <p class="cards__item-date">{{item.lastUpdated}}</p>
        </div>
        <div class="cards__item__button-thumbs">
          <ButtonThumbsUp
            :class='{isActive: isActiveUp}'
            :disabled='disabledThumb'
            @click='
              initialVoteUp = 1;
              initialVoteDown = 0;
              disabled = false;
              isActiveDown = false;
              isActiveUp = true;
              persist(item.votes.positive, item.name);
            '
          />
          <ButtonThumbsDown
            :class='{isActive: isActiveDown}'
            :disabled='disabledThumb'
            @click='
              initialVoteDown = 1;
              initialVoteUp = 0;
              disabled = false;
              isActiveDown = true;
              isActiveUp = false;
              persistLess(item.votes.negative, item.name);
            '
          />
          <ButtonVoteNow
            :disabled='disabled'
            @click='
              initialVoteDown === 1 ? item.votes.negative -= 1 : item.votes.negative += 0;
              initialVoteUp === 1 ? item.votes.positive += 1 : item.votes.positive += 0;
              disabled = true;
              disabledThumb = true;
              isActiveDown = false;
              isActiveUp = false;
              updatedCTAButton();
            '
            :ctaButton='vote'
          />
        </div>
        <ProgressBar :negativeValue=item.votes.negative :positiveValue=item.votes.positive />
      </li>
    </ul>
  </div>
</template>

<script>
import ButtonThumbsDown from '../buttons/ButtonThumbsDown.vue';
import ButtonThumbsUp from '../buttons/ButtonThumbsUp.vue';
import ButtonVoteNow from '../buttons/ButtonVoteNow.vue';
import ProgressBar from '../common/ProgressBar.vue';
import SelectGrid from '../common/SelectGrid.vue';

export default {
  components: {
    ButtonThumbsDown,
    ButtonThumbsUp,
    ButtonVoteNow,
    ProgressBar,
    SelectGrid,
  },
  data() {
    return {
      cards: this.$store.state.cards.data,
      disabled: true,
      disabledThumb: false,
      initialVoteDown: 0,
      initialVoteUp: 0,
      isActiveUp: false,
      isActiveDown: false,
      percentCurrent: 0,
      selectedGrid: false,
      thanksVote: 'Thank you for your vote!',
      voteAgain: 'Vote Again',
      vote: 'Vote now',
      votedMsg: 'You already voted!',
    };
  },
  methods: {
    persist(positive, name) {
      localStorage.setItem(name, Number(positive) + 1);
    },

    persistLess(negative, name) {
      localStorage.setItem(name, Number(negative) - 1);
    },
    selectValue() {
      if (this.selectedGrid === true) {
        this.selectedGrid = false;
      } else {
        this.selectedGrid = true;
      }
    },
    updatedCTAButton() {
      this.vote = this.thanksVote;

      setTimeout(() => {
        this.vote = this.voteAgain;
      }, 3000);
    },
  },
  name: 'CardsItemImage',
};
</script>

